___CSS_LOADER_EXPORT___.push([module.id, `.create-project-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.create-project-container h1 {
    text-align: center;
    margin-bottom: 20px;
}

.create-project-form .form-group {
    margin-bottom: 15px;
}

.create-project-form label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}

.create-project-form input,
.create-project-form textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.create-project-form textarea {
    resize: vertical;
}

.create-project-form .form-actions {
    text-align: center;
    margin-top: 20px;
}

.create-project-form .btn {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.create-project-form .btn:hover {
    background-color: #0056b3;
